.header {
    display: grid;
    grid-template-columns: 1fr 9fr 1fr;
    box-shadow: 2px 2px 3px #90909083;
    /* border-bottom-width: 1px;
    border-bottom-style: solid;
    border-bottom-color: rgb(224, 224, 224); */
    align-items: center;
    height: 90px;
    font-size: 16px;
}

.header img {
    padding-left: 25px;
}

.navbar {
    display: grid;
    grid-column: 2/3;
}

.navAssisted {
    display: grid;
    grid-column: 3/4;
}