.content {
    display: grid;
    width: 1200px;
    margin: 10px auto;
    padding: 20px 0;
    border-top: 2px solid #ECEEF2;
    border-bottom: 2px solid #ECEEF2;
    grid-template-columns: 35% 65%;
}

.formButtonBox {
    display: grid;
    justify-items: start;
    align-items: start;
    grid-column: 1;
    grid-row: 3;
}

.submitBtn {
    margin-top: 15px;
    margin-bottom: 15px;
    margin-left: 10px;
    padding: 10px 20px;
    border-radius: 6px;
    background-color: #AE0018;
    color: #ffffff;
    font-size: 16px;
    border: none;
}

.submitBtn:hover,
.submitBtn:active {
    background-color: #AE0018;
}

.privacyPolicy {
    display: grid;
    grid-column: 1/3;
    grid-row: 4;
    justify-items: start;
    align-self: center;
    padding-left: 10px;
    padding-bottom: 10px;
    font-size: 16px;
}

.privacyPolicy a {
    color: #AE0018;
}


.leftColumn {
    display: grid;
    grid-column: 1;
    grid-template-rows: 15% 85%;
}

.leftColumn__header {
    display: grid;
    font-size: 30px;
    justify-self: start;
    align-self: start;
    padding-bottom: 7px;
    color: #AE0018;
    border-bottom: 2px solid #ECEEF2;
}

.leftColumn__text {
    display: grid;
    text-align: start;
    padding-top: 10px;
    font-size: 16px;
}

.rightColumn {
    display: grid;
    grid-column: 2;
    grid-template-columns: 50% 50%;
    align-items: start;
    grid-template-rows: 54px auto auto;
    grid-auto-rows: auto;
}

.rightColumn__row {
    display: grid;
    grid-column: 1/3;
    align-self: start;
}

.inputBox1,
.inputBox2,
.inputBox3 {
    display: grid;
    height: 41px;
    padding-left: 10px;
    padding-right: 10px;
    padding-bottom: 10px;
    margin-bottom: 3px;
}

.inputBox3 {
    height: 142px;
}

.textArea1,
.textArea2 {
    padding: 10px;
    resize: none;
    font-size: 16px;
    border-color: #AE0018;
    border-radius: 6px;
    outline: none;
    height: 19px;
}

.textArea2 {
    height: 120px;
}

.textArea1:focus,
.textArea2:focus,
.textArea3:focus {
    background-color: #ECEEF2;
}