.container {
  display: flex;
  flex-direction: column;
  height: 100vh;
  background-color: #ffffff;
  z-index: 0;
  font-size: 22px;
}

.profile-content {
  flex: 1;
  padding-top: 90px;
  z-index: 2;
  margin-bottom: -280px;
  padding-bottom: 280px;
}

.header {
  position: fixed;
  flex: 0;
  top: 0;
  width: 100%;
  z-index: 3;
  background-color: #ffffff;
}

.content {
  flex: 1;
  padding-top: 90px;
  z-index: 2;
  margin-bottom: -280px;
  padding-bottom: 280px;
  background-color: #ffffff;
  background-color: transparent;
}

.footer {
  flex: 0 0 auto;
  z-index: 1;
  width: 100%;
  position: fixed;
  bottom: 0;
  left: 0;
}


























