.content {
    display: grid;
    /* grid-row: 2fr 1fr; */
    text-decoration: none;
    margin-bottom: 17px;

}

.headerBox {
    display: flex;
    /* margin-top: 17px; */
    padding-top: 17px;
    padding-bottom: -6px;
    /* width: 1200px; */
    margin: auto;
    justify-self: center;
}

.itemBox {
    display: grid;
    /* grid-row: 2/3; */
    grid-template-columns: 1fr 1fr;
    margin-top: 19px;
    margin-bottom: 25px;
    grid-column-gap: 28px;
}

.itemBoxLeft {
    display: grid;
    grid-row-gap: 13px;
    grid-template-rows: 1fr 1fr;
    justify-content: end;
    align-items: end;
}

.itemBoxRight {
    display: grid;
    grid-row-gap: 13px;
    grid-template-rows: 1fr 1fr;
    justify-content: start;
    align-items: end;
}

