code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

h1 {
  font-size: 40px;
  font-weight: 500;
  margin-block: 0;
}

h2 {
  font-size: 32px;
  font-weight: 400;
  margin-block: 0;
}