.content {
    display: grid;
    grid-template-rows: 50% 50%;
    grid-template-columns: 40% 60%;
    background-color: #ECEEF2;
    height: 400px;
    grid-template-columns: 700px auto;
    box-shadow: 2px 2px 3px #90909083;
}

.imgBox {
    display: grid;
    grid-column: 2/3;
    grid-row: 1/3;
    height: 400px;
    justify-content: end;
    align-content: start;
    overflow: hidden;
}

.img {
    display: grid;
    grid-column: 2/3;
    grid-row: 1/3;
    width: 100%;
    height: 1100px;
    object-fit: cover;
    align-content: end;
    margin-top: -100px;
    padding-right: -50px;
    z-index: 3;

}