.content {
    display: grid;
    grid-template-columns: auto auto;
    grid-template-rows: auto auto;
    row-gap: 10px;
    align-items: center;
}

.itemHead {
    display: grid;
    grid-row: 1/2;
    grid-column: 1/2;
    justify-content: center;
    /* grid-column-gap: 28px; */
}

.itemText {
    display: grid;
    justify-content: center;
    grid-row: 2/3;
    grid-column: 1/2;
    /* grid-column-gap: 28px; */
    /* margin-right: 30px; */
    font-size: 24px;
    font-weight: 300;
    text-align: justify;
}

.img {
    display: grid;
    grid-row: 2/3;
    grid-column: 2/3;
    align-content: center;
    margin-left: 30px;


}

.img img {
    /* background-color: rgb(6, 6, 6); */
    height: 222px;
    width: 410px;
    object-fit: contain;
}