.item {
    display: grid;
    justify-content: start;
    align-content: center;
    padding-left: 25px;
    z-index: 2;
    text-transform: uppercase;
    /* font-style: italic; */
    /* font-weight: 400; */
    grid-column: 1/2;
    font-weight: 500;
    height: 100%;
    font-size: 40px;
    grid-row: 2/3;
}

.border {
    display: grid;
    height: auto;
    width: 3px;
    /* justify-self: end; */
    grid-column: 2/3;
    grid-row: 1/2;
    background-color: #AE0018;
    margin-left: 34px;
    margin-top: 10px;
    margin-bottom: 8px;
    /* z-index: 3; */
}

